import React from 'react';
import { SAMPLE_PARAGRAPHS } from "../../data/sampleParagraphs.js";
import "./App.css";
import Nav from "../Nav/Nav";
import Landing from "../Landing/Landing";
import ChallengeSection from "../ChallengeSection/ChallengeSection";
import Footer from "../Footer/Footer";

const totaltime = 60;


const DefaultState = {
    selectedParagraph: "",
    timerStarted: true,
    timerRemaining: totaltime,
    Characters: 0,
    Words: 0,
    wpm: 0,
};

class App extends React.Component {
    state = DefaultState;


    fetchNewParagraphFallback = () => {
        const data =
            SAMPLE_PARAGRAPHS[
                Math.floor(Math.random() * SAMPLE_PARAGRAPHS.length)
            ];
        
        const selectedParagraphArray = data.split("");
        const testinfo = selectedParagraphArray.map((selectedLetter) => {
            return {
                testLetter: selectedLetter,
                status: "notAttempted",
            };
        });
        this.setState({
            ...DefaultState,
            selectedParagraph: data,
            testinfo,
        });
    };
    

    fetchNewParagraph = () => {
        fetch("http://metaphorpsum.com/paragraphs/1/9")
            .then((response) => response.text())
            .then((data) => {
                // Once the api results are here, break the selectedParagraph into test info
                const selectedParagraphArray = data.split("");
                const testInfo = selectedParagraphArray.map(
                    (selectedLetter) => {
                        return {
                            testLetter: selectedLetter,
                            status: "notAttempted",
                        };
                    }
                );

                // Update the testInfo in state
                this.setState({
                    ...DefaultState,
                    selectedParagraph: data,
                    testInfo,
                });
            });
    };

    componentDidMount() {
        // As soon as the component mounts, load the selected paragraph from the API
        this.fetchNewParagraphFallback();
    }

    startAgain = () => this.fetchNewParagraphFallback();


    startTimer = () => {
        this.setState({ timerStarted: true });
        const timer = setInterval(() => {
            if (this.state.timerRemaining > 0) {
                // speed
                const timeSpent = totaltime - this.state.timerRemaining;
                const wpm = timeSpent > 0
                    ? (this.state.words / timeSpent) * totaltime
                    : 0;

                this.setState({
                    timerRemaining: this.state.timerRemaining - 1,
                    wpm: parseInt(wpm),

                });
            } else {
                clearInterval(timer);
            }
            
        }, 1000);
    };

    handleuserInput =(inputValue)=> {
        if (!this.state.timerStarted) this.startTimer();

        const characters = inputValue.length;
        const words = inputValue.split(" ").length;
        const index = characters - 1;

        if (index < 0) {
            this.setState({
                testInfo: [
                    {
                        testLetter: this.state.testInfo[0].testLetter,
                        status: "notAttempted",
                    },
                    ...this.state.testInfo.slice(1),
                ],
                characters,
                words,
            });

            return;
        }

        if (index >= this.state.selectedParagraph.length) {
            this.setState({
                characters,
                words,
            });
            return;
        }

        // Make a copy
        const testinfo = this.state.testinfo;
        if (!(index === this.state.selectedParagraph.length - 1))
            testinfo[index + 1].status = "notAttempted";

        // Check for mistake
        const isMistake = inputValue[index] === testinfo[index].testLetter;

        // Update the testInfo
        testinfo[index].status = isMistake ? "correct" : "incorrect";

        // Update the state
        this.setState({
            testinfo,
            words,
            characters,
        });
    };
    


    render() {
        return (
            <div className="app">
                <Nav />
                <Landing/>
                <ChallengeSection
                selectedParagraph = {this.state.selectedParagraph}
                Words = {this.state.Words}
                Characters = {this.state.Characters}
                wpm={this.state.wpm}
                timerRemaining = {this.state.timerRemaining}
                timerStarted = {this.state.timerStarted}   
                testinfo={this.state.testinfo}
                handleuserInput = {this.handleuserInput}    
                />
                <Footer />
            </div>
        );
    };
};


export default App;